.header {
  padding: 1rem 24rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 0 2rem;
  border-bottom: 0.1rem solid #f2f3f3;
}
.header-link {
  color: gray;
  font-size: 2rem;
  font-weight: bold;
}
.header-language-button {
  color: gray;
  margin-right: 8rem;
  font-size: 1.6rem;
  display: inline-block;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media screen and (max-width: 1024px) {
  .header {
    padding: 1rem;
  }
  .header-language-button {
    margin-right: 0rem;
  }
}
