html {
  font-size: 62.5%;
}
a,
a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
  color: #2962ff;
}
li {
  padding-bottom: 1.2rem;
}

.app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
  text-align: left;
  color: #2c3e50;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.container {
  margin: 2rem 24rem;
}
@media screen and (max-width: 1024px) {
  .container {
    margin: 2rem 1rem;
  }
}
